import React,{useState,useEffect} from 'react';
 import Video from '../../images/7.jpg';
 import Fade from 'react-reveal/Fade';
 import m1 from "./Cards/1.png";
 import m2 from "./Cards/2.png";
 import m3 from "./Cards/3.png";
 import m4 from "./Cards/4.png";
 import m5 from "./Cards/5.png";
 import m6 from "./Cards/6.png";
// import Video from '../../videos/video.mp4';
 import sliderImage1 from '../../images/IMB_FhzIUa.gif';
 import sliderImage3 from '../../images/make-animated-nft-for-you.gif';

 import sliderImage2 from '../../images/nft-art.gif';
 import '@splidejs/splide/dist/css/themes/splide-default.min.css';
// or
import '@splidejs/splide/dist/css/themes/splide-sea-green.min.css';
// or
import '@splidejs/splide/dist/css/themes/splide-skyblue.min.css';

import { Button } from '../ButtonElement';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import { 
    HeroContainer,
    HeroBg,
    VideoBg,
    DivCardI,
    HeroContent,
    HeroH1,
    HeroP,
    HeroSlider,
    HeroBtnWrapper,
    ArrowForward,
    DivCard,
    ArrowRight,
    

} from './HeroElements';
import back2 from "../../images/Coming.png"


const HeroSection = () => {
    const [hover, setHover]= useState(false);
    const [width, setWidth] = useState(window.screen.width);
    let primaryOptions = {
        type      : 'loop',
        width     : 520,
        height    : 300,
    	autoplay  : true,
        perPage   : 2,
        perMove   : 1,
        gap       : '2rem',
        pagination: true,
    };



    const onHover = () => {
        setHover(!hover);
    }
    return (
        <HeroContainer id="home">
            <HeroBg>
                 {/* <VideoBg autoPlay loop muted src={Video} type='video/mp4'/>  */}
                 <VideoBg  src={Video} />
                </HeroBg>
        <HeroContent className="justify-content-center ">
            <DivCardI>
            <HeroH1>MINTING NFT <span style={{color:"#DA0000"}}>MADE</span> EASIER </HeroH1>
            <HeroP className="">
                Mint a very rare rich kid of NFT for as low as 0.3 ETH  
            </HeroP>
            <HeroBtnWrapper>
                <Button 
                className="text-decoration-none"
                to="collection" 
                onMouseEnter={onHover} 
                onMouseLeave={onHover}
                primary='true'
                dark='true'
                smooth={true}
                duration={500}
                spy={true}
                exact='true'
                offset={-80}
                >
                    View Collection {hover? <ArrowForward /> : <ArrowRight/>}
                </Button>
            </HeroBtnWrapper>
            </DivCardI>
            <DivCard>
            {/* <img style={{width: "650px", height:"400px"}} src={Video}></img>
             */}
             <div>

             </div>
             <Fade right>
         
             <Splide className=""  options={ primaryOptions } style={{}}>
                <SplideSlide >
                    <img  src={m1}   alt="Image 1"/>
                </SplideSlide>
                <SplideSlide>
                <img src={m2}  alt="Image 1"/>
                </SplideSlide>
                <SplideSlide>
                <img src={m3}  alt="Image 1"/>
                </SplideSlide>
                <SplideSlide>
                <img src={m4}  alt="Image 1"/>
                </SplideSlide>
                <SplideSlide>
                <img src={m5}  alt="Image 1"/>
                </SplideSlide>
                <SplideSlide>
                <img src={m6}  alt="Image 1"/>
                </SplideSlide>
            </Splide> 
             </Fade>
        </DivCard>
        </HeroContent>
       
   
        </HeroContainer>

    )  
}

export default HeroSection;
